<template>
	<div class="login-container">
		<div class="login-header">
			<div class="login-logo">
				<img src="../../images/login_logo.png" alt="" @click="tabclick('introducePage')">
				<img class="login-logo-line" src="../../images/login_header_line.png"> XPE 智能办公平台
			</div>
		</div>
		<div class="login-box" :class="{'login-box-padding-bottom':!loginWay||!canRegister}" v-if="!toShow">
			<div class="title">
				管理后台登录
			</div>
			<!-- 账号登录 -->
			<div class="login-form" v-if="loginWay">
				<div class="login-tab-link clear">
					<div class="login-tab-link-item" :class="{'login-tab-link-item-checked':accountType}" @click="changeAccountType">
						<span>账号登录</span>
					</div>
					<div class="login-tab-link-item" :class="{'login-tab-link-item-checked':!accountType}" @click="changeAccountType">
						<span>手机登录</span>
					</div>
				</div>
				<!-- 账号登录 -->
				<div v-if="accountType">
					<div class="login-Item">
						<input type="input" placeholder="账号" autocomplete="new-password" class="login-input" name="" v-model="userId" :class="{'login-input-error':userIdError}" @keyup.enter="inputBlur('userId','password')">
						<em v-if="userIdError" class="error">{{userIdMsg}}</em>
					</div>
					<div class="login-Item">
						<div class="login-Item-passwordInputbox">
							<input :type="userPwdVisibleType" ref="password" autocomplete="new-password" placeholder="密码" class="login-input" v-model="userPwd" @keyup.enter="inputBlur('userPwd','validate')">
							<i class="icon-eye" :class="{'icon-eye-visible':userPwdVisible}" @click="userPwdVisible=!userPwdVisible"></i>
						</div>
						<em v-if="userPwdError" class="error">{{userPwdMsg}}</em>
						<router-link to="/findpwd"><em class="forgetpwd">忘记密码</em></router-link>
					</div>
					<!-- 验证码 -->
					<div class="login-Item" v-if="showValidate">
						<input type="input" placeholder="验证码" ref="validate" class="left login-input login-input-code" :class="{'login-input-error':userValidateCodeError}" name="" v-model="userValidateCode" @keyup.enter="inputBlur('userValidateCode','')">
						<div class="right login-code-img" @click="refreshCode">
							<s-identify :identifyCode="identifyCode" :contentWidth="128" :contentHeight="48"></s-identify>
						</div>
						<em v-if="userValidateCodeError" class="error">{{userValidateCodeErrorMsg}}</em>
					</div>
				</div>
				<!-- 手机登录 -->
				<div v-if="!accountType">
					<div class="login-Item">
						<input type="input" placeholder="手机号" autocomplete="new-password" class="login-input" :class="{'login-input-error':phoneNumError}" name="" v-model="phoneNum" @blur="checkPhonenum" @keyup.enter="inputBlur('phoneNum','phoneNumVal')" maxlength='11'>
						<em v-if="phoneNumError" class="error">{{phoneNumErrorMsg}}</em>
					</div>
					<!-- 手机验证码 -->
					<div class="login-Item">
						<input type="input" maxlength='6' ref="phoneNumVal" placeholder="输入验证码" class="left login-input login-input-code" :class="{'login-input-error':phoneNumValError}" name="" v-model="phoneNumVal" @blur="checkPhoneval" @keyup.enter="inputBlur('phoneNumVal','')">
						<input class="right login-code-btn" :class="{'login-code-btn-disabled1':getPhoneCode||phoneNumError||phoneNum==''}" type="button" :value="getPhoneCodeText" @click="getPhoneCodeClick">
						<em v-if="phoneNumValError" class="error">{{phoneNumValErrorMsg}}</em>
					</div>
				</div>
				<div class="autologin">
					<span class="checkbox" :class="{'checkbox-checked':autoLogin}" @click="changeAutoLogin"></span>自动登录
				</div>
				<div>
					<input class="login-btn" :class="{'login-btn-disabled':loginOk}" type="button" value="登  录" @click="loginClick">
				</div>
			</div>
			<!-- 二维码登录 -->
			<div class="login-code" v-show="!loginWay">
				<!-- 说明图 -->
				<div class="login-code-infoimg" :class="{'login-code-infoimg-show':!loginWay}">
					<img src="../../images/login_infoimg.png">
				</div>
				<div class="login-code2-img">
					<div class="login-code2-imgbox" v-if="ewCodeFail==0">
						<img :src="erweima">
						<h1>扫描二维码登录网页后台</h1>
					</div>
					<div class="login-code-success" v-if="ewCodeFail==1">
						<div class="login-code-success-content">
							<img src="../../images/login_scansuccess.png">
							<h2>扫描成功!</h2>
							<h3>请勿刷新本页，按手机提示操作!</h3>
						</div>
					</div>
					<div class="login-code-disable" v-if="ewCodeFail==2">
						<div class="login-code-disable-content">
							<h2>二维码失效</h2>
							<input @click="regetEncode" class="login-btn-refresh" type="button" name="" value="刷新">
						</div>
					</div>
					<div class="login-code-disable" v-if="ewCodeFail==3">
						<div class="login-code-disable-content">
							<h4>浏览器版本过低，不支持扫码登录，请更新浏览器版本</h4>
						</div>
					</div>
					<div class="login-code-disable" v-if="ewCodeFail==4">
						<div class="login-code-disable-loading">
							<i class="el-icon-loading"></i>
						</div>
					</div>
				</div>
			</div>
			<div class="login-tab" :class="{'login-tab-pc': !loginWay}" @click="changeLoginWay">
			</div>
			<div class="register-bottominfo" v-show="loginWay &&canRegister">
				还没有账号？<em @click="toRegister">立即注册</em>
			</div>
		</div>
		<!-- 选择公司 -->
		<div class="login_company_list_box" v-if="toShow">
			<div class="title_img_box">
				<img src="../../images/icon_ts.png" alt="">
			</div>
			<div class="login_company_list" v-for="(item,index) of loginCompanyList" :key="index" @click="toSelectCompany(item)">
				<span>{{item.mechanism_name}}</span>
			</div>
		</div>
		<!-- 设置密码 -->
		<el-dialog title="设置密码" :visible.sync="createDialogShow" width="570px" center :append-to-body='true' :close-on-click-modal='false' :close-on-press-escape='false' :show-close="false">
			<div class="dialog_content">
				<div class="form-group margin-bottom-none">
					<div class="form-group-inputboxgroup clear">
						<div class="form-group-inputbox">
							<h2 class="form-group-inputbox-title">
								<span class="account-icon icon-required"> </span>新密码

                					<em>(密码不少于6位)</em>
							</h2>
							<div class="form-group-inputbox-selectbox select-normal-40 left">
								<input class="form-group-inputbox-inputtext" type="password" v-model="dialogParam.newpwd">
							</div>
							<span class="form-group-inputbox-error" v-if="newpwdError">{{newpwdErrorMsg}}</span>
						</div>
					</div>
					<div class="form-group-inputboxgroup clear">
						<div class="form-group-inputbox">
							<h2 class="form-group-inputbox-title"><span class="account-icon icon-required"></span>再次输入新密码</h2>
							<div class="form-group-inputbox-selectbox select-normal-40 left">
								<input class="form-group-inputbox-inputtext" type="password" v-model="dialogParam.newpwd1">
							</div>
							<span class="form-group-inputbox-error"></span>
						</div>
					</div>
				</div>
			</div>
			<div slot="footer" class="dialog_footer clear">
				<button class="dialog-btn-submit btn-blue" @click="dialogSubmitClick">确认</button>
			</div>
		</el-dialog>
		<!-- <vue-particles color="#e1e1e1" :particleOpacity="0.5" :particlesNumber="15" shapeType="circle" :particleSize="15" linesColor="#cfcfcf" :linesWidth="1" :lineLinked="true" :lineOpacity="0.4" :linesDistance="650" :moveSpeed="2" :hoverEffect="true" hoverMode="grab" :clickEffect="false" clickMode="push">
    </vue-particles> -->
	</div>
</template>
<script>
	//验证码插件
	import SIdentify from "../../components/identify.vue";
	import {
		sassLogin,
		verifySaasCodeWeb,
		getMenu,
		sendSaasCode,
		getEcode,
		apiLogin,
		apiLoginByphone,
		apiAutoLogin,
		apiLogout,
		getQrcode,
		apiChangepwdbyPhone,
		getVersion
	} from "../../config/api";
	import Modal from "../../components/modal.vue";
	//import VueParticles from 'vue-particles'
	import { mapState, mapMutations } from "vuex";
	import { dialogConfirm, isBroswer } from "../../config/mUtils";
	import { setStore, getStore, removeStore } from "../../config/mUtils";
	import api from "../../config/url.js";
	import axios from "axios";
	import url from "../../config/url"; //引用url.js
	//md5加密
	import md5 from "js-md5";
	export default {
		name: "HelloWorld",
		data() {
			return {
				toShow: false,
				loginCompanyList: [],

				showInfoimg: false,
				loginWay: true, //登录方式(账号二维码切换)，默认账号登录
				accountType: true, //登录方式（账号/手机），默认账号
				userId: "", //用户名
				userIdError: false, //用户名错误提示
				userIdMsg: "", //用户名错误提示内容
				userPwd: "", //用户密码
				userPwdError: false, //用户密码错误提示
				userPwdMsg: "", //用户密码错误提示内容
				userPwdVisible: false,
				phoneNum: "", //手机号
				phoneNumErrorMsg: "", //手机号格式错误提示内容
				phoneNumVal: "", //手机短信验证码
				phoneNumValErrorMsg: "", //手机验证码错误提示内容
				getPhoneCode: false, //是否点击了发送验证码
				getPhoneCodeText: "获取验证码",
				wrongTime: 0, //输错内容次数，3次时需要输验证码
				userValidateCode: "", //用户输入的验证码
				userValidateCodeError: false, //用户输入的验证码提示
				userValidateCodeErrorMsg: "", //用户输入的验证码提示内容
				autoLogin: false, //是否自动登录
				//验证码
				identifyCodes: "123456789ABCDEF",
				identifyCode: "", //当前验证码
				showAlert: false, //是否显示提示框
				alertText: "修改成功",
				alertType: "success",
				ewCodeFail: 0, //0=>扫描二维码1=>扫描成功2=>二维码失效

				websock: null,

				erweima: "",
				canRegister: true,
				//-------------------------设置密码弹窗
				createDialogShow: false,
				newpwdErrorMsg: "",
				dialogParam: {
					newpwd: "",
					newpwd1: ""
				}
			};
		},
		components: {
			SIdentify,
			Modal
			//VueParticles
		},
		created() {
			if(getStore("htcx_autologin") === "true") {
				if(getStore("htcx_webtoken") != null && getStore("htcx_uid") != null) {
					apiAutoLogin().then(res => {
						if(res.data.msgNo == 1) {
							let userInfo = JSON.parse(getStore("htcx_userInfo"));
							userInfo.url = res.data.payload;
							setStore("htcx_userInfo", userInfo);
							setStore("htcx_userUrl", res.data.payload);
							this.RECORD_USERINFO(getStore("htcx_userInfo")); //用户信息存全局
							this.RECORD_USERURL(res.data.payload.url); //用户信息存全局
							this.$router.push({
								path: "/home/homepage"
							});
						}
					});
				}
			} else {
				setStore("htcx_webtoken", ""); //存储本次token
				setStore("htcx_uid", ""); //uid
				window.localStorage.removeItem("htcx_server_address");
				window.localStorage.removeItem("htcx_server_key");
				api.resetServer();
			}
			this.makeCode(this.identifyCodes, 4);
		},
		mounted() {},
		computed: {
			...mapState(["userInfo", "userUrl", "userName"]),
			phoneNumError: function() {
				if(this.phoneNum != "") {
					return !/^1\d{10}$/gi.test(this.phoneNum);
				} else {
					return false;
				}
			},
			phoneNumValError: function() {
				if(this.phoneNumVal == "" || this.phoneNumVal.length == 6) {
					return false;
				} else {
					return true;
				}
			},
			userPwdVisibleType: function() {
				if(this.userPwdVisible) {
					return "text";
				} else {
					return "password";
				}
			},
			//是否显示图片验证码
			showValidate: function() {
				return this.wrongTime >= 3 ? true : false;
			},
			//登录按钮是否可点击
			loginOk: function() {
				if(this.loginWay) {
					if(this.showValidate) {
						if(this.accountType) {
							if(
								this.userId != "" &&
								this.userPwd != "" &&
								this.userValidateCode != ""
							) {
								return true;
							} else {
								return false;
							}
						}
					} else {
						if(this.accountType) {
							if(this.userId != "" && this.userPwd != "") {
								return true;
							} else {
								return false;
							}
						} else {
							if(
								this.phoneNum != "" &&
								this.phoneNumVal != "" &&
								!this.phoneNumValError &&
								!this.phoneNumError
							) {
								return true;
							} else {
								return false;
							}
						}
					}
				} else {
					return false;
				}
			},
			//弹窗验证---------------------------------
			newpwdError: function() {
				if(
					this.dialogParam.newpwd == "" ||
					this.dialogParam.newpwd.length < 6 ||
					this.dialogParam.newpwd1 == "" ||
					this.dialogParam.newpwd1.length < 6
				) {
					return true;
				} else {
					return false;
				}
			}
		},
		methods: {
			...mapMutations([
				"RECORD_USERINFO",
				"RECORD_USERURL",
				"RECORD_USERNAME",
				"SET_SERVER"
			]),

			//验证码方法
			randomNum(min, max) {
				return Math.floor(Math.random() * (max - min) + min);
			},
			//刷新验证码
			refreshCode() {
				this.identifyCode = "";
				this.makeCode(this.identifyCodes, 4);
			},
			//制作验证码
			makeCode(o, l) {
				for(let i = 0; i < l; i++) {
					this.identifyCode += this.identifyCodes[
						this.randomNum(0, this.identifyCodes.length)
					];
				}
			},
			//检验手机号格式
			checkPhonenum() {
				if(this.phoneNumError) {
					this.phoneNumErrorMsg = "请输入正确的11位手机号码";
				} else {
					this.phoneNumErrorMsg = "";
				}
			},
			//检验手机验证码格式
			checkPhoneval() {
				if(this.phoneNumValError) {
					this.phoneNumValErrorMsg = "手机验证码格式错误";
				} else {
					this.phoneNumValErrorMsg = "";
				}
			},
			//修改登录方式，二维码/账号
			changeLoginWay() {
				this.loginWay = !this.loginWay;
				if(!this.loginWay) {
					this.ewCodeFail = 0;
					this.getEncode();
				}
			},
			//重新获取二维码
			regetEncode() {
				this.loginWay = false;
				//this.websock.onclose();
				this.getEncode();
				this.ewCodeFail = 0;
			},
			//获取二维码
			getEncode() {
				let _that = this;
				var timer = "";
				if("WebSocket" in window) {
					// 打开一个 web socket
					_that.websock = new WebSocket(url.encode_url);
					_that.websock.onopen = function() {};
					_that.websock.onmessage = function(evt) {
						let data = JSON.parse(evt.data);
						if(data["message_type"] == "qrcode_url") {
							_that.ewCodeFail = 4;
							getQrcode({
								fd: data.fd
							}).then(res => {
								if(res.data.msgNo == 1) {
									_that.erweima = res.data.payload.url;
									_that.ewCodeFail = 0;
									timer = setTimeout(function() {
										_that.ewCodeFail = 2;
									}, 60000);
								}
							});
						} else if(data["message_type"] == "scan_success") {
							//扫描成功
							_that.ewCodeFail = 1;
							clearTimeout(timer);
						} else if(data["message_type"] == "login_success") {
							//登录成功
							clearTimeout(timer);
							console.log(data);
							setStore("htcx_server_address", data.data.domain_name); //存储本次token;
							setStore("htcx_server_key", data.data.key); //存储本次token
							setStore("htcx_server_table_token", data.data.table_token); //存储本次table_token

							api.setServer(data.data.domain_name);
							_that.getMenuData(data.data.user_id, data.data.phone);
						}

						/*ws.onclose = function()
						{
						  // 关闭 websocket
						  alert("连接已关闭...");
						};*/
					};
				} else {
					_that.ewCodeFail = 3;
				}
			},
			//修改登录方式，手机/账号
			changeAccountType() {
				this.accountType = !this.accountType;
			},
			//是否自动登录
			changeAutoLogin() {
				this.autoLogin = !this.autoLogin;
			},
			//获取短信验证码
			getPhoneCodeClick() {
				if(!this.getPhoneCode && !this.phoneNumError && this.phoneNum != "") {
					sendSaasCode({
						phone: this.phoneNum
					}).then(res => {
						if(res.data.msgNo == 1) {
							dialogConfirm("success", "发送成功");
							var that = this;
							this.getPhoneCode = true;
							var num = 60;
							var timer = setInterval(function() {
								num--;
								that.getPhoneCodeText = num + "秒后重新获取";
								if(num === 0) {
									that.getPhoneCodeText = "获取验证码";
									that.getPhoneCode = false;
									clearInterval(timer);
								}
							}, 1000);
						} else {
							dialogConfirm("fail", res.data.message.split("错误码")[0]);
						}
					});
				} else if(this.getPhoneCode) {
					// this.$message({
					//   message:"发送太频繁啦",
					//   type:'warning'
					//  })
					dialogConfirm("info", "发送太频繁啦");
				}
			},
			//登录按钮
			loginClick() {
				if(!this.loginOk) return;
				//账号登录
				if(this.accountType && this.wrongTime >= 3) {
					if(
						this.userValidateCode.toLowerCase() != this.identifyCode.toLowerCase()
					) {
						this.userValidateCodeError = true;
						this.userValidateCodeErrorMsg = "验证码错误";
						return;
					}
				}
				this.userValidateCodeError = false;
				if(this.loginWay && this.accountType) {
					this.loginByPwd();
				} else if(this.loginWay && !this.accountType) {
					this.loginByPhone();
				}
			},
			//账号登录
			loginByPwd() {
				sassLogin({
					username: this.userId,
					password: this.userPwd
				}).then(res => {
					// console.log(data);
					if(res.data.msgNo == 1) {
						let data = res.data.payload;
						if(data.length > 1) {
							this.loginCompanyList = data;
							this.toShow = true;
						} else {
							if(data[0].status == 2) {
								setStore("htcx_server_address", data[0].domain_name); //存储本次token;
								setStore("htcx_server_key", data[0].key); //存储本次token
								setStore("htcx_server_table_token", data[0].table_token); //存储本次table_token

								api.setServer(data[0].domain_name);
								this.getMenuData(data[0].user_id, data[0].phone);
							} else {
								dialogConfirm("fail", "该账号所属公司未审核通过,请联系相关人员");
							}
						}
					} else {
						this.wrongTime++;
						this.userIdError = true;
						this.userIdMsg = "账号或密码错误";
						dialogConfirm("fail", "账号或密码错误，请重新填写");
					}
				});
			},
			//手机号码登录
			loginByPhone() {
				verifySaasCodeWeb({
					phone: this.phoneNum,
					code: this.phoneNumVal
				}).then(res => {
					if(res.data.msgNo == 1) {
						let data = res.data.payload;
						if(data.length > 1) {
							this.loginCompanyList = data;
							this.toShow = true;
						} else {
							if(data[0].status == undefined) {
								dialogConfirm("info", "该账号不存在");
							} else {
								if(data[0].status == 2) {
									setStore("htcx_server_address", data[0].domain_name); //存储本次token;
									setStore("htcx_server_key", data[0].key); //存储本次token
									setStore("htcx_server_table_token", data[0].table_token); //存储本次table_token

									api.setServer(data[0].domain_name);
									this.getMenuData(data[0].user_id, data[0].phone);
								} else {
									this.$router.push({
										name: "registerInfo",
										query: {
											phone: data[0].phone
										}
									});
								}
							}
						}
					} else {
						dialogConfirm("fail", res.data.message.split("错误码")[0]);
					}
				});
			},

			//获取侧边栏信息
			getMenuData(id, phone) {
				getMenu({
					phone: phone,
					user_id: id
				}).then(
					res => {
						if(res.data.msgNo == 1) {
							// console.log(res.data);
							setStore("htcx_webtoken", res.data.payload.webToken); //存储本次token
							setStore("htcx_autologin", this.autoLogin); //下次是否自动登录
							setStore("htcx_uid", res.data.payload.uid); //uid
							setStore("htcx_userInfo", res.data.payload); //uid
							setStore("htcx_userUrl", res.data.payload.url); //uid
							setStore("htcx_userName", res.data.payload.username); //uid
							this.RECORD_USERINFO(res.data.payload); //用户信息存全局
							this.RECORD_USERURL(res.data.payload.url); //用户信息存全局
							this.RECORD_USERNAME(res.data.payload.username); //用户信息存全局
							if(res.data.payload.have_password == 1) {
								this.createDialogShow = true;
								this.dialogParam = {
									newpwd: "",
									newpwd1: "",
									phone: phone
								};
							} else {
								this.$router.push({
									path: "/home/homepage"
								});
							}
						} else {
							dialogConfirm("fail", res.data.message.split("错误码")[0]);
							window.localStorage.removeItem("htcx_server_address");
							window.localStorage.removeItem("htcx_server_key");
							api.resetServer();
						}
					},
					error => {
						window.localStorage.removeItem("htcx_server_address");
						window.localStorage.removeItem("htcx_server_key");
						api.resetServer();
					}
				);
			},

			//提示弹窗
			showAlertFunc(alertText, alertType, alertTime) {
				this.showAlert = true;
				this.alertText = alertText;
				this.alertType = alertType;
				var that = this;
				var timer = setTimeout(function() {
					that.showAlert = false;
				}, alertTime);
			},
			//弹窗-----------修改密码
			dialogSubmitClick() {
				if(
					this.dialogParam.newpwd == "" ||
					this.dialogParam.newpwd.length < 6 ||
					this.dialogParam.newpwd1 == "" ||
					this.dialogParam.newpwd1.length < 6
				) {
					this.newpwdErrorMsg = "新密码不少于6位";
				} else {
					if(this.dialogParam.newpwd != this.dialogParam.newpwd1) {
						this.newpwdErrorMsg = "两次密码不一致";
					} else {
						apiChangepwdbyPhone({
							phone: this.dialogParam.phone,
							password: this.dialogParam.newpwd
						}).then(res => {
							if(res.data.msgNo == 1) {
								dialogConfirm("success", "修改成功");
								this.$router.push({
									path: "/home/homepage"
								});
							} else {
								dialogConfirm("fail", res.data.message.split("错误码")[0]);
							}
						});
					}
				}
			},
			//去注册账号
			toRegister() {
				this.$router.push({
					path: "/register"
				});
			},
			inputBlur(name, ref) {
				if(name == "userPwd") {
					if(this.showValidate) {
						this.$refs[ref].focus();
					} else {
						this.loginClick();
					}
				} else if(name == "userValidateCode") {
					this.loginClick();
				} else if(name == "phoneNumVal") {
					this.loginClick();
				} else {
					this.$refs[ref].focus();
				}
			},
			tabclick(name) {
				// this.$router.push({
				//   name: name
				// });
				window.location.href = 'http://www.pewinner.com';
				// window.location.href='/pe-introduce'
			},
			toSelectCompany(item) {
				// if (item.status == 2) {
				//   setStore("htcx_server_address", item.domain_name); //存储本次token;
				//   setStore("htcx_server_key", item.key); //存储本次token
				//   api.setServer(item.domain_name);
				//   this.getMenuData(item.user_id, item.phone);
				// } else {
				//   dialogConfirm("fail", "该账号不存在");
				// }

				if(item.status == undefined) {
					dialogConfirm("info", "该账号所属公司未审核通过,请联系相关人员");
				} else {
					if(item.status == 2) {
						setStore("htcx_server_address", item.domain_name); //存储本次token;
						setStore("htcx_server_key", item.key); //存储本次token
						setStore("htcx_server_table_token", item.table_token); //存储本次table_token

						api.setServer(item.domain_name);
						this.getMenuData(item.user_id, item.phone);
					} else {
						dialogConfirm("info", "该账号所属公司未审核通过,请联系相关人员");
						// this.$router.push({
						//   name: "registerInfo",
						//   query: {
						//     phone: item.phone
						//   }
						// });
					}
				}
			}
		}
	};
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
	@import "../../style/modal.scss";
	@import "../../style/login1";
	.login-code {
		position: relative;
	}
	
	.login-code-infoimg {
		position: absolute;
		left: -400px;
		top: -144px;
		transition: all 0.6s;
		animation: myfirst 1s;
	}
	
	.login-code-success {
		text-align: center;
		img {
			margin-top: 76px;
		}
		h2 {
			color: #282828;
			font-size: 24px;
			margin-top: 30px;
			line-height: 1em;
		}
		h3 {
			color: #a0a4aa;
			font-size: 14px;
			margin-top: 16px;
		}
	}
	
	@keyframes myfirst {
		from {
			opacity: 0;
			left: -420px;
		}
		to {
			opacity: 1;
			left: -400px;
		}
	}
	/* 弹窗样式 */
	
	.form-group-inputbox {
		width: 470px;
		margin-right: 0px;
	}
	
	.form-group-inputboxgroup {
		margin-bottom: 25px;
	}
	
	.dialog-btn-submit.btn-blue-border {
		margin-right: 20px;
	}
	
	.login_company_list_box {
		display: flex;
		flex-direction: column;
		width: 430px;
		max-height: 85%;
		position: absolute;
		left: 50%;
		top: 55%;
		transform: translate(-50%, -50%);
	}
	
	.title_img_box {
		// display: flex;
		// justify-items: center;
		// flex-direction: row;
		text-align: center;
		margin-bottom: 20px;
		img {
			width: 265px;
			height: 31px;
		}
	}
	
	.login_company_list {
		height: 96px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: rgba(255, 255, 255, 1);
		margin-bottom: 25px;
		border-radius: 4px;
		span {
			color: #35383a;
		}
		cursor: pointer;
	}
	
	.login_company_list:hover {
		span {
			color: #1787fb;
		}
		box-shadow: 0px 1px 14px 0px rgba(116, 149, 177, 0.5);
	}
</style>


